"use client";

import { useMemo, useState } from "react";

import { AddToProjectSteps } from "@/components/builders/AddToProjectSteps";
import { CodeOutput } from "@/components/builders/CodeOutput";
import { ConfigPanel } from "@/components/builders/ConfigPanel";
import Link from "next/link";

export default function INFTBuilderPage() {
  const [network, setNetwork] = useState("0G Testnet");
  const [contractAddress, setContractAddress] = useState(
    process.env.NEXT_PUBLIC_INFT_ADDRESS ?? "0x..."
  );

  const soliditySnippet = useMemo(
    () => `// Guard: require verified human agent
// Network: ${network}
// iNFT: ${contractAddress}

function onlyVerified() internal view {
    require(inft.isVerified(msg.sender), "Agent not verified");
}`,
    [network, contractAddress]
  );

  const reactSnippet = useMemo(
    () => `import { useReadContract } from "wagmi";
import { inftAbi } from "./abi";

export function useIsVerified(wallet: \`0x\${string}\`) {
  return useReadContract({
    address: "${contractAddress}",
    abi: inftAbi,
    functionName: "isVerified",
    args: [wallet],
  });
}`,
    [contractAddress]
  );

  return (
    <div className="mx-auto max-w-3xl space-y-8">
      <div>
        <Link href="/dashboard" className="text-sm text-white/60 hover:text-white/90">
          ‚Üê Dashboard
        </Link>
        <h1 className="mt-2 text-2xl font-semibold">iNFT Builder</h1>
        <p className="mt-1 text-sm text-white/60">
          Generate Solidity guards and React hooks for Human Agent iNFT verification.
        </p>
      </div>

      <ConfigPanel
        title="Configure"
        fields={[
          {
            id: "network",
            label: "Network",
            type: "select",
            value: network,
            onChange: setNetwork,
            options: [
              { value: "0G Testnet", label: "0G Testnet" },
              { value: "0G Mainnet", label: "0G Mainnet" },
            ],
          },
          {
            id: "contract",
            label: "iNFT contract address",
            value: contractAddress,
            onChange: setContractAddress,
            placeholder: "0x...",
          },
        ]}
      />

      <section>
        <h2 className="mb-3 text-lg font-semibold">Generated code</h2>
        <div className="space-y-4">
          <CodeOutput title="Solidity guard" code={soliditySnippet} />
          <CodeOutput title="React hook" code={reactSnippet} />
        </div>
      </section>

      <AddToProjectSteps
        steps={[
          { num: 1, title: "Deploy iNFT contract", body: "Use the contracts package or deploy your own." },
          { num: 2, title: "Add guard to your contract", body: "Include onlyVerified() in functions that require human verification." },
          { num: 3, title: "Use the hook in your app", body: "Call useIsVerified(wallet) to check verification status." },
        ]}
      />
    </div>
  );
}
